import Base from '../Base';
import Adapter from 'enzyme-adapter-react-16';
import {shallow} from 'enzyme';
import * as enzyme from 'enzyme';
import {useNavigation} from '@react-navigation/native';
import {NativeStackNavigationProp} from '@react-navigation/native-stack/lib/typescript/src/types';
import {TextInput, Button} from 'react-native';
import {QuizContext} from './../QuizContext';
enzyme.configure({adapter: new Adapter()});
const navigation = useNavigation<NativeStackNavigationProp<any>>();
jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});

describe('should render a form with three text inputs, two selections, and one submit', () => {
  const wrapper = shallow(<Base />);
  it('component renders correctly', () => {
    expect(wrapper).toMatchSnapshot();
  });
  it('should update the name when input is entered', () => {
    const nameInput = wrapper.find(TextInput).at(0);
    const emailInput = wrapper.find(TextInput).at(1);
    const phoneInput = wrapper.find(TextInput).at(2);

    nameInput.simulate('changeText', 'John Doe');
    emailInput.simulate('changeText', 'johndoe@example.com');
    phoneInput.simulate('changeText', '1234567890');

    expect(wrapper.find(Button).at(0).prop('disabled')).toBe(false);
  });
});
